header {
  background-image: linear-gradient(175deg, rgba(151, 237, 37, 0.251), rgba(61, 143, 10, 0.922));
  box-shadow: 0 12px 34px -11px rgb(38 20 19 / 10%);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(2px);
  font-weight: 500;
  font-size: 1.3rem;

}

